<template>
  <v-container fluid class="d-flex">
    <v-row>
      <v-col
        cols="12"
        xl="3"
        lg="3"
        md="4"
        sm="6"
        xs="12"
        v-for="item in listItens"
        :key="item.id"
      >
        <v-card dark class="mx-auto ma-1" max-width="21rem">
          <v-img
            class="align-end"
            :src="item.imagem"
            contain
            height="15rem"
          />
          <v-card-title>{{ item.displayName }}</v-card-title>

          <v-card-text>
            <span >Desenvolvimento: {{ item.language }}</span>
          </v-card-text>
          <v-card-actions>
            <v-btn
              color="primary"
              text
              @click="redirect(item.html_url)"
            >
              Ir ao repositório
            </v-btn>
            <v-spacer />
            <v-btn
              icon
              @click="show = !show"
            >
              <v-icon>{{ show ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
            </v-btn>
          </v-card-actions>
          <v-expand-transition>
            <div v-show="show">
              <v-divider />
              <v-card-text>
                {{item.description}}
              </v-card-text>
            </div>
          </v-expand-transition>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
import axios from 'axios'

export default {
  data: () => ({
    show: false,
    panel: [],
    listItens: []
  }),
  methods: {
    redirect(url) {
      window.open(`${url}`, '_blank');
    }
  },
  async mounted() {
    try {
      const {data} = await axios.get(`https://api.github.com/users/m4rlonr/repos`);
      data.map(item => {
        item.displayName = item.name.charAt(0).toUpperCase() + item.name.slice(1).split("_").join(" ")
        item.imagem = `https://raw.githubusercontent.com/${item.owner.login}/${item.name}/master/${item.name}.png`
      })
      this.listItens = data;
    } catch (error) {
      console.log("Deu erro")
    }
  }
}
</script>